import common from "../../common/common.js";

export default {
	data() {
		return {
			trailerList:[]
		}
	},
	methods: {
		
	},
	onLoad() {
		//查询猜你喜欢
		uni.showLoading({
			// 防止用户点击某一些按钮
			mask:true,
			title:"请稍后"
		})
		var serverUrl = common.serverUrl+ "/search/list?"+ common.qqStr+"&keywords=&page=&pageSize=";
		uni.request({
			url: serverUrl ,
			method: "POST",
			success: (res) => {
				if(res.data.status == 200){
					this.trailerList= res.data.data.rows;
					console.log(this.trailerList)
				}
			},
			complete:() => {
				uni.hideLoading();
			}
		});
	}
}
